import { useState } from "react";
import { Row, Col, Drawer } from "antd";
import Container from "../../common/Container";
import { SvgIcon } from "../../common/SvgIcon";
import { Button } from "../../common/Button";
import {
  HeaderSection,
  LogoContainer,
  Burger,
  NotHidden,
  NavContainer,
  Menu,
  CustomNavLinkSmall,
  Label,
  Outline,
  Span,
  MobileMenuItem,
} from "./styles";

const Header = () => {
  const [visible, setVisibility] = useState(false);

  const toggleButton = () => {
    setVisibility(!visible);
  };

  const MenuItem = () => {
    const scrollTo = (id: string) => {
      const element = document.getElementById(id) as HTMLDivElement;
      element.scrollIntoView({
        behavior: "smooth",
      });
      setVisibility(false);
    };
    return (
      <>
        <MobileMenuItem onClick={() => scrollTo("about")}>
          <span>Nosotros</span>
        </MobileMenuItem>
        <MobileMenuItem onClick={() => scrollTo("mission")}>
          <span>Misin</span>
        </MobileMenuItem>
        <MobileMenuItem onClick={() => scrollTo("product")}>
          <span>Servicios</span>
        </MobileMenuItem>
                <div style={{ marginTop: '1rem' }}>
          <Button variant="header" onClick={() => scrollTo("contact")}>
            Contacto
          </Button>
        </div>
      </>
    );
  };

  return (
    <HeaderSection>
      {/* Logo */}
      <LogoContainer to="/" aria-label="homepage">
        <SvgIcon src="logo.svg" width="180px" height="48px" className="logo-with-outline" />
      </LogoContainer>

      {/* Navegacin Desktop */}
      <NavContainer>
        <Span onClick={() => scrollTo("about")}>Nosotros</Span>
        <Span onClick={() => scrollTo("mission")}>Misin</Span>
        <Span onClick={() => scrollTo("product")}>Servicios</Span>
        <Button variant="header" onClick={() => scrollTo("contact")}>
          Contacto
        </Button>
      </NavContainer>

      {/* Burger Menu Mobile */}
      <Burger onClick={toggleButton} className="burger-icon">
        <Outline />
      </Burger>

      {/* Drawer Mobile */}
      <Drawer closable={false} open={visible} onClose={toggleButton} className="modern-drawer">
        <Col style={{ marginBottom: "2.5rem" }}>
          <Label onClick={toggleButton} className="drawer-header">
            <Col span={12}>
              <Menu>Menu</Menu>
            </Col>
            <Col span={12}>
              <Outline />
            </Col>
          </Label>
        </Col>
        <MenuItem />
      </Drawer>
    </HeaderSection>
  );

  function scrollTo(id: string) {
    const element = document.getElementById(id) as HTMLDivElement;
    element.scrollIntoView({
      behavior: "smooth",
    });
    setVisibility(false);
  }
};

export default Header;
